.I {
    /* margin-top: 80px; */
    /* margin-bottom: 80px; */
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .I p {
    font-size: 2rem;
    padding: 0.5rem;
    /* font-weight: bold; */
    letter-spacing: 0.1rem;
    text-align: left;
    overflow: hidden;
  }
  .I p span.typed-text {
    font-weight: normal;
    text-align: left;
    font-size: 2rem;
    font-style: italic;
    /* color: #9966cc; */
  }
  .I p span.cursor {
    display: inline-block;
    background-color: #ccc;
    margin-left: 0.1rem;
    width: 3px;
    animation: blink 1s infinite;
  }
  .I p span.cursor.typing {
    animation: none;
  }
  @keyframes blink {
    0% {
      background-color: #ccc;
    }
    49% {
      background-color: #ccc;
    }
    50% {
      background-color: transparent;
    }
    99% {
      background-color: transparent;
    }
    100% {
      background-color: #ccc;
    }
  }